<template>
  <div class="weekly-rank">
    <template v-if="weeklyData">
      <RankItem v-for="(item, index) in weeklyData" :item="item" :key="index+`w`"
        @RankItemClick="RankItemClick($event)"></RankItem>
      <div class="end">——the end——</div>
    </template>
    <template v-else>
      <van-loading type="spinner" color="#6b6b6b" />
    </template>
  </div>
</template>

<script>
  import Vue from 'vue'
  import RankItem from "@/components/RankItem.vue";
  export default {
    name: 'weekly-rank',
    data() {
      return {
        weeklyData: null
      }
    },
    beforeRouteEnter(to, from, next) {
      let weeklyData = JSON.parse(localStorage.getItem('weeklyData'))
      if (weeklyData && (Date.now() - weeklyData.time) / 1000 / 60) {
        next(vm => {
          vm.weeklyData = weeklyData.data
        })
      } else {
        Vue.axios.get(
            `https://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/ranklist?num=10&strategy=weekly&udid=26868b32e808498db32fd51fb422d00175e179df&vc=83`
          )
          .then(res => {
            next(vm => {
              vm.weeklyData = res.data.itemList
              localStorage.setItem('weeklyData', JSON.stringify({
                time: Date.now(),
                data: vm.weeklyData
              }))
            })
          })
      }
    },
    methods: {
      RankItemClick($event) {
        this.$router.push('/playingdetailpage');
        this.$store.commit('setPlayingPageDate', $event)
        this.$store.commit('setShowBottomBar', false)
      },
    },
    components: {
      RankItem
    }
  }
</script>

<style lang="less" scoped>
  .weekly-rank {
    .van-loading {
      text-align: center;
    }

    .end {
      font-weight: bold;
      color: #6b6b6b;
      text-align: center;
      padding: 10px 0;
    }
  }
</style>